  "temperature": {
    values: function (value, alpha) {
      const hsl = 240 - Math.round((1 - value) * 240);
      return `hsla(${hsl}, 100%, 50%, ${alpha})`;
    }
  },

  "red-purple-blue": {
    values: function (value, alpha) {
      const hsl = 240 + Math.round((1 - value) * 120);
      return `hsla(${hsl}, 100%, 50%, ${alpha})`;
    }
  },

